.cat-info {
  width: 100%;
  gap: 15px;
  margin-top: 20px;
}

p {
  width: 500px;
}

.text, .loader {
  font-weight: 600;
}

.loader-container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 34px;
  height: 34px;
  transform-origin: center;
  border: 3px solid #ff3d00;
  border-top-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: auto;
  animation: rotationBack .5s linear infinite;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.invisible {
  display: none;
}

.visible {
  display: flex;
}

.error {
  color: red;
}

.loaders {
  display: none;
}

